}

.assignments-card{
    width:350px;
    height:380px;
    /* background-color: aqua; */
    border-radius: 12px;
    border: 1px solid gray;
    background: #fff;
    background-image: linear-gradient(to left, #6ca1ff70, #4d79ff29, #b7c0f900);
    box-shadow: 0 4px 10px #abababc9;
 
 
    /* position: absolute; */
   
}
.assign-btn{
    width: 80%;
    height:50px;
font-weight: 900;
font-size: 18px;
    display: flex;
   align-items: center;
   gap:1rem;
 padding:0.2rem;
 margin-left: 1rem;

}
.time{
    width:100%;
    height:100%;
