  /* *****************************************************card************************************************ */
 
  .Card {
    text-align: left;
    padding: 3px;
    width: 290px;
    height: 180px;
    font-weight: 200;
  
    margin-top: 32px;
    margin-left: 12px;
    border-radius: 7px;
    border-bottom: 12px solid #012951;
    box-shadow: -8px 6px 17px -1px #8b8aa1;
  }

  .course-name {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .course-name a {
    font-weight: 700;
    width: 60%;
    color: #33308b;
    height: 100%;
  
    width: 60%;
  }
 
  
  .cource-batch {
    border-bottom: 1px dashed gray;
  }
  
  .id-logo {
    margin-left: 72px;
  }
  
  .id-logo ::after {
    content: "2";
    width: 15px;
    height: 15px;
  }
  
