*{
    padding: 0;
    margin: 0;
}
.main{
    padding: 12px;
    height: 100vh;
}
.layout{
    display: flex;
}

.sidebar{
    margin-top: 20px;
    min-height:100%;
    width:300px;
    border-radius: 6px;
    background: 	white;
    box-shadow: 0 0 2px gray;
    margin-right: 20px;
    color:black;
}
.content{
    width:100%;
    height: 100%;  
}

.body{
    height:85vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px gray;
    background: white;
}
.menu{
    margin-top: 50px;
}
.menu-item{
  padding: 10px;
    margin-top: 15px;
}
.menu-item a{
    color:black;
    text-decoration: none;
    font-size: 1.2rem;
}
.menu-item i{
    font-size: 1.2rem;
    margin: 0 15px;
}
.active{
 margin: 1rem;
background: #012951;
padding:10px;
border-radius: 12px;
}
.active a,.active i{
    color:white;
}
.menu-item:hover{
color:white;
margin: 1rem;
background: #012951;
padding:10px;
border-radius: 12px;

}




/* header content */
.header{
  margin-bottom: 20px;
    box-shadow: rgb(2 2 2 / 8%) -12px 6px 0px 7px;
 width:100%;
 height:39%;
}
.header-content{
display: flex;
align-items: center;
height:40px;
width:50%;
justify-content: flex-end;
margin: 1px 20px;
}
.BSlogo img{
    width:20%;
    /* height:80%; */
   padding:2rem;
  margin:3rem;
}
.rounded-circle{
  background:#1a1855; 
   font-size:28px; 
   font-weight:600; 
   width:60px;
    height:60px;
  color:white
}
.header-content a{
    text-decoration: none; 
    color:    #012951;
    margin:0 10px;
    text-transform:capitalize;
    font-size: 1.3rem;
}
.header-content i{
    margin-right: 5px;
    font-size: 1.2rem;
    /* color: red; */
}

