
.courseNameSection{
    margin:1%;
    width: 90%;
    height:37%;
border-radius: 8px;
    background: #fff;
    background-image: linear-gradient(to left, #6ca1ff70, #4d79ff29, #b7c0f900);
    box-shadow: 0 4px 10px #abababc9;
    margin-bottom: 2%;
    padding: 2%;
    margin-top:16px;
}
.Remainingfeebox{
    width:100%;
    height:auto;
    display: flex;
    justify-content: space-between;
    align-items: center;

}
.Remainingfee {
 width:50%;
 height: 100%;
 justify-content: space-between;
 /* align-items: center; */
 font-weight: 400;
 
}
.loadDiv{
    margin:0.3rem;
    width:100%;
    height:15px;
display: flex;
    border-radius:12px;
    border: 1px solid gray;
    background-color: gainsboro;
}
.loadDivOne
{   width:80%;
    height:100%;
    background-color: rgb(9, 158, 9);
    border-radius:12px;
    border-right: 0px solid gray;
}
.completingPercent{
  font-weight: 400;
}

.Merncard{
    width: 40%;
    height: 39%;
margin: 2rem;
padding:1rem;
border-radius: 12px;
box-shadow: 0 4px 10px #abababc9;
}
.cardN{
    width:70%;
    height:min-content;
    /* padding:0.4rem; */
    display: flex;
    gap: 1rem;
    justify-content: space-around;
}
.cardN div{
    font-weight: 300;
    color:#33308b;

}
.attendbox{
    width:40px;
    height:40px;
    border-radius: 50%;
    background-color: rgb(22, 189, 44);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -22%;
    margin-left: 98%;

}
.attendbox1{
    width:30px;
    height:30px;
    border-radius: 50%;
  background-color:white;
    display: flex;
    justify-content: center;
    align-items: center;
}
.attendbox1 div{
    font-size: 9px;
    color:
    rgb(22, 189, 44);
}
.currentBatch{
    margin-top: 8%;
    margin-left: 10%;
    font-weight: 400;
    color:rgb(61, 59, 59);
}
.currentBatch div{
    margin: 0.2rem;
    align-items: flex-start !important;
}
.trainername {
   padding: 0.1rem;
  width: 100%;
   
}

.resource{
    border-top: 1px dashed gray;
    /* display: flex; */
}

.myRes{
color:#33308b;
}
.myRes svg{
    width:8%;
}
.Merncard:hover{
 box-shadow: inset;
}


/* resourcess */


.resourcedata{
   
    background-color: white;
    width:450px;
    height:450px;
    border-radius: 12px;
    box-shadow: 0px 0px 11px 13px #3b3a3a14;

}
.resourcedatabox{
    width: 100%;
    height:40px;
    display: flex;
    justify-content: space-around;

}
.resourcedatabox1{
    padding:1rem;
    width: 100%;
    height:40px;
    display: flex;
    justify-content: space-around;
    align-items: center;

}
